
.em {
  display: inline-block;
  width: 1em;
}
.em_5 {
  display: inline-block;
  width: .5em;
}
body,
ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6,
dl,
dt,
dd,
input,
form {
  margin: 0px;
  padding: 0px;
  list-style: none;
}
body {
  background-color: #f0eff6;
  color: #372f2c;
  font-family: "Microsoft Yahei";
}
.fl {
  float: left;
}
.fr {
  float: right;
}
body {
  background: #f0eff5;
}
#device_detail .device {
  background: #fff;
  padding: 0 20px;
}
#device_detail .device li {
  overflow: hidden;
  padding: 10px 0;
  border-bottom: 1px solid #ddd;
}
#device_detail .device li .fr {
  color: #999;
}
#device_detail .price {
  margin-top: 20px;
  background: #fff;
  padding: 10px 20px;
  overflow: hidden;
}
#device_detail .price .fr {
  color: #467cd4;
}
#device_detail .gif {
  margin-top: 20px;
  background: #fff;
  padding: 0 20px;
}
#device_detail .gif .tit {
  overflow: hidden;
  padding: 10px 0;
  border-bottom: 1px solid #ddd;
}
#device_detail .gif .tit .fr {
  color: #999;
}
#device_detail .gif .gif-list {
  overflow: hidden;
  padding: 10px 0;
}
#device_detail .gif .gif-list .fl {
  color: #999;
}
#device_detail .gif .gif-list .fr {
  color: #999;
  margin-top: 10px;
}
#device_detail .oth {
  margin-top: 20px;
  background: #fff;
  overflow: hidden;
  padding: 10px 20px;
}
#device_detail .quit {
  margin-top: 20px;
  background: #fff;
  color: #f00;
  padding: 10px;
  text-align: center;
  cursor: pointer;
}



